.d1 {
    width: 100%;
    color: #dddddd;
    background-color: #17212b;
}

.tit {
    color: #0076ff;
    font-size: 24px;
    padding-top: 30px;
    font-weight: bold;
    padding-left: 26px;
}

.ctgr1 {
    padding-left: 11px;
    margin-top: 20px;
    font-size: 18px;
    display: grid;
    font-weight: 600;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.ctgr1_tab {
    display: inline-block;
    padding-top: 5px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 5px;
}

.ctgr2 {
    margin-top: 20px;
}

.ctgr2_div {
    display: grid;
    text-align: center;
    font-weight: 600;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.ctgr2_div_tab {
    display: inline-block;
    padding-top: 5px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 5px;
}

.ctgr3 {
    text-align: center;
    margin-top: 25px;
}

.ctgr3_div {
    display: grid;
    font-weight: 600;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.ctgr3_div_tab {
    display: inline-block;
    padding-top: 5px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 5px;
}

.item {
    margin-top: 25px;
}

.item_div {
    color: #dddddd;
    display: grid;
    text-decoration: none;
    padding-top: 10px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 10px;
    grid-template-columns: 70px 1fr;
    grid-template-rows: 70px 1fr;
}

.item_div_img {
    border-radius: 3px;
    background-color: #0076ff;
}

.item_div_text {
    margin-left: 10px;
}

.item_div_text_tit {
    font-weight: 600;
}

.item_div_text_desc {
    padding-top: 3px;
}
